import { HttpClient, HttpResponse } from '@angular/common/http'
import { ChangeDetectorRef, Component, Input } from '@angular/core'
import { ActivatedRoute, Router } from '@angular/router'
import { NgbActiveModal, NgbCalendar, NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import * as Color from 'color'
import { capitalize } from 'lodash'
import { Observable } from 'rxjs'
import { delay, finalize, flatMap, take, tap } from 'rxjs/operators'
import { IUser, TransactionType } from 'src/app/common/api-interfaces'
import { ListComponent } from 'src/app/common/list.component'
import { Transaction } from 'src/app/common/models/accounting/transaction.model'
import { Wallet } from 'src/app/common/models/accounting/wallet.model'
import { User } from 'src/app/common/models/core/user.model'
import { ConfirmationResult, ConfirmationService } from 'src/app/common/services/confirmation.service'
import { SessionService } from 'src/app/common/services/session.service'
import { ToastrService } from 'src/app/common/services/toastr.service'
import { TransactionService } from 'src/app/common/services/transaction.service'
import { TransactionViewComponent } from 'src/app/common/transaction/transaction-view/transaction-view.component'
import { buildWalletOwnerAccountNumber, calculateDeductableAmount } from 'src/app/common/transactions.mixins'
import { environment } from 'src/environments/environment'

@Component({
    selector: 'merchant-user-profile',
    templateUrl: 'merchant-user-profile.component.html',
})
export class MerchantUserProfileComponent extends ListComponent<Transaction> {
    @Input()
    public associate: Partial<IUser>
    public user: IUser | null
    public apiUrl: string
    public displayName = environment.displayName
    public appColor = Color(getComputedStyle(document.body).getPropertyValue('--primary').trim()).lighten(0.1)
    public calculateDeductableAmount = calculateDeductableAmount
    public transactionsLoading = new Set<string>()
    public activeTab: 'overview' | 'send-payment' | 'send-invoice' = 'overview'
    public transaction: Partial<Transaction> = {
        amount: '',
        method: 'internal',
        type: 'transfer',
    }

    constructor(
        http: HttpClient,
        ngbModal: NgbModal,
        changeDetector: ChangeDetectorRef,
        route: ActivatedRoute,
        router: Router,
        session: SessionService,
        confirmation: ConfirmationService,
        public calendar: NgbCalendar,
        public activeModal: NgbActiveModal,
        public translate: TranslateService,
        private transactionService: TransactionService,
        private toastr: ToastrService
    ) {
        super(http, ngbModal, changeDetector, route, router, session, confirmation)
    }

    public find(): Observable<HttpResponse<any[]>> {
        return this.session.userStream.pipe(
            delay(0),
            take(1),
            tap(user => {
                this.user = new User(user)
                this.apiUrl = `/associations/${this.user.id}/users/${this.associate.id}/transactions`
                this.transaction.currency = user.preferredCurrency
            }),
            flatMap(() => super.find())
        )
    }

    public viewTransaction(
        transaction: Transaction,
        options: { activeTab?: 'overview' | 'beneficiary' | 'attachments' } = {}
    ): NgbModalRef {
        const modal = this.ngbModal.open(TransactionViewComponent, {
            windowClass: 'modal-primary',
        })
        const transactionView = modal.componentInstance as TransactionViewComponent
        transactionView.transaction = { ...transaction }
        if (!!options.activeTab) {
            transactionView.activeTab = options.activeTab
        }

        transactionView.onCancel.subscribe(() => {
            modal.dismiss()
            this.updateEvent.next()
        })

        transactionView.onUpdate.subscribe(() => {
            this.updateEvent.next()
        })

        return modal
    }

    public formatType(type: TransactionType): string {
        return type.slice(0, 1).toUpperCase()
    }

    public buildBeautifiedType(transaction: Transaction): string {
        switch (transaction.type) {
            case 'transfer':
                if (this.isReceiver(transaction)) {
                    return this.translate.instant('common.transfer-from')
                }
                return this.translate.instant('common.transfer-to')
            case 'deposit':
                return transaction.method === 'card'
                    ? this.translate.instant('common.unload-from')
                    : this.translate.instant('common.deposit-from')
            case 'withdrawal':
                if (transaction.method === 'card') {
                    return this.translate.instant('common.topup-to')
                }
                return this.translate.instant('common.withdrawal-to')
            case 'card-order':
            case 'account-fee':
            case 'payment':
                return this.translate.instant('common.payment-to')
            case 'conversion':
                if (this.isReceiver(transaction)) {
                    return this.translate.instant('common.conversion-from')
                }
                return this.translate.instant('common.conversion-to')
            case 'trade':
                if (this.isReceiver(transaction)) {
                    return transaction.currency.code === 'USD'
                        ? this.translate.instant('common.credit')
                        : this.translate.instant('common.buy')
                }
                return transaction.currency.code === 'USD'
                    ? this.translate.instant('common.debit')
                    : this.translate.instant('common.sell')
            case 'investment':
                if (this.isReceiver(transaction)) {
                    return this.translate.instant('common.investment-from')
                }
                return this.translate.instant('common.investment-to')
            default:
                return capitalize(transaction.type)
        }
    }

    public isReceiver(transaction: Transaction): boolean {
        return (
            !!transaction.counterWallet &&
            !!transaction.counterWallet.user &&
            !!this.user &&
            transaction.counterWallet.user.id === this.user.id
        )
    }

    public determineSubject(transaction: Transaction): string {
        switch (transaction.type) {
            case 'card-order':
                return `${environment.displayName} <span class="text-muted">/ ${this.translate.instant(
                    'common.card'
                )}</span>`
            case 'account-fee':
                return `${environment.displayName} <span class="text-muted">/ ${this.translate.instant(
                    'common.account-fee'
                )}</span>`
            case 'deposit':
            case 'withdrawal':
                return this.beautifyMethod(transaction)
            case 'payment':
                return environment.displayName
            case 'transfer':
                if (transaction.isPlatformTransfer()) {
                    return environment.displayName
                } else if (this.isReceiver(transaction)) {
                    return this.buildAccountHolder(transaction.baseWallet!)
                }
            // eslint-disable-next-line no-fallthrough
            default:
                return this.buildAccountHolder(transaction.counterWallet!)
        }
    }

    public beautifyMethod(transaction: Transaction): string {
        switch (transaction.method) {
            case 'local':
            case 'swift':
                if (
                    transaction.type === 'withdrawal' &&
                    transaction.beneficiary &&
                    transaction.beneficiary.accountHolder &&
                    transaction.beneficiary.accountNumber
                ) {
                    return `${transaction.beneficiary.accountHolder} <span class="text-muted">/ ${transaction.beneficiary.accountNumber}</span>`
                }
                return `${
                    this.user?.type === 'business'
                        ? this.translate.instant('common.corporate')
                        : this.translate.instant('common.personal')
                } ${this.translate.instant('common.bank-account')}`
            case 'card':
                const maskedPan = transaction.card?.maskedPan
                return (
                    'Card' +
                    (maskedPan
                        ? ` <span class="text-muted">/ ${maskedPan.slice(0, 4)} ${maskedPan.slice(
                              4,
                              8
                          )} ${maskedPan.slice(8, 12)} ${maskedPan.slice(12)} </span>`
                        : '')
                )
            default:
                return capitalize(transaction.method)
        }
    }

    public markAsPaid(item: Transaction): void {
        if (item.method === 'crypto') {
            this.viewTransaction(item)
            return
        }
        this.transactionsLoading.add(item.id)
        this.transactionService
            .changeStatus(item.id, 'processing')
            .pipe(
                finalize(() => {
                    this.transactionsLoading.delete(item.id)
                })
            )
            .subscribe(() => {
                this.toastr.success(this.translate.instant('common.deposit-mark-as-paid'))
                this.updateEvent.next()

                this.subscriptions.add(
                    this.confirmation
                        .show({
                            type: 'success',
                            text: this.translate.instant('common.thank-you-for-notifying'),
                            confirmText: this.translate.instant('common.i-will-upload-a-receipt'),
                            cancelText: this.translate.instant('common.i-will-do-it-later'),
                            cancelClass: 'warning',
                        })
                        .subscribe(result => {
                            if (result === ConfirmationResult.CONFIRMED) {
                                this.viewTransaction(item, { activeTab: 'attachments' })
                            }
                        })
                )
            })
    }

    private buildAccountHolder(wallet: Wallet): string {
        return `${
            wallet.user ? `${wallet.user!.name}` : this.displayName
        } <span class="text-muted">/ ${buildWalletOwnerAccountNumber(wallet, environment.appName)}</span>`
    }
}
