import { HttpClient, HttpResponse } from '@angular/common/http'
import { ChangeDetectorRef, Component } from '@angular/core'
import { ActivatedRoute, Router } from '@angular/router'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { Observable, of } from 'rxjs'
import { flatMap, tap, withLatestFrom } from 'rxjs/operators'
import { UserFormModalComponent } from 'src/app/admin/users/user-form-modal/user-form-modal.component'
import { ConfirmationService } from 'src/app/common/services/confirmation.service'
import { SessionService } from 'src/app/common/services/session.service'
import { IUser, UserType } from '../../common/api-interfaces'
import { ListComponent } from '../../common/list.component'
import { MerchantUserProfileComponent } from '../merchant-user-profile/merchant-user-profile.component'

@Component({
    selector: 'merchant-customer-list',
    templateUrl: 'merchant-customer-list.component.html',
})
export class MerchantCustomerListComponent extends ListComponent<IUser> {
    public types: UserType[] = ['personal', 'business']

    protected apiUrl = '/users'
    protected apiQuery = ['approvedAt', 'type', 'createdAt']
    protected formComponent = UserFormModalComponent

    constructor(
        http: HttpClient,
        ngbModal: NgbModal,
        changeDetector: ChangeDetectorRef,
        route: ActivatedRoute,
        router: Router,
        session: SessionService,
        confirmation: ConfirmationService,
        public translate: TranslateService
    ) {
        super(http, ngbModal, changeDetector, route, router, session, confirmation)
    }

    public find(): Observable<HttpResponse<any[]>> {
        return of(undefined).pipe(
            withLatestFrom(this.session.userStream),
            tap(([_, user]) => {
                this.apiUrl = `/users/${user.id}/associates`
            }),
            flatMap(() => super.find())
        )
    }

    public openUserProfile(associate: Partial<IUser>): void {
        const modal = this.ngbModal.open(MerchantUserProfileComponent, {
            backdrop: 'static',
            windowClass: 'modal-primary',
            size: 'lg',
        })

        const component = modal.componentInstance as MerchantUserProfileComponent
        component.associate = associate
    }
}
